import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Table, Popconfirm, Col } from 'antd';
import { classes } from 'dump/data';
import FormSearchClasses from './FormSearchClasses';

const columns = [
  {
    title: 'Tn',
    dataIndex: 'name',
    key: 'code',
  },
  {
    title: 'S SV',
    dataIndex: 'numberStudent',
    key: 'name',
  },
  {
    title: 'Nin kha',
    dataIndex: 'year',
    key: 'name',
  },
  {
    title: 'Tn khoa',
    dataIndex: 'specializedCode',
    key: 'name',
  },
  {
    title: 'CN',
    dataIndex: 'specializedTrainingCode',
    key: 'name',
  },
  {
    title: 'Thao tc',
    dataIndex: 'operation',
    render: (text, record) =>
      classes.length >= 1 ? (
        <Fragment>
          <BtnEdit href={`#classes/form/edit/${record.code}`} />
          <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
            <BtnDelete />
          </Popconfirm>
        </Fragment>
      ) : null,
  },
];

class ListClassesPage extends Component {
  state = {};

  render() {
    return (
      <Card title="DS lp hc">
        <Row>
          <Col span={24}>
            <FormSearchClasses />
          </Col>
          <Col span={24}>
            <BtnAddNew href="#classes/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={classes} columns={columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListClassesPage />
  </Page>
);



